<template>
  <div class="add-risk-level">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="120"
      class="form-content"
    >
      <FormItem label="等级代码" prop="levelCode">
        <Input v-model="formValidate.levelCode" placeholder="请输入等级代码" :disabled="!newItem"></Input>
      </FormItem>
      <FormItem label="等级名称" prop="levelName">
        <Input v-model="formValidate.levelName" placeholder="请输入等级名称"></Input>
      </FormItem>
      <FormItem label="评定周期（月）" prop="evalCycle">
        <Input v-model="formValidate.evalCycle" placeholder="请输入评定周期（月）"></Input>
      </FormItem>
      <FormItem label="风险色阶">
          <ColorPicker v-model="formValidate.color"/>
      </FormItem>
      <FormItem label="控制措施" prop="controlMeasure">
        <Input
          v-model="formValidate.controlMeasure"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入业务描述"
        ></Input>
      </FormItem>
      <FormItem >
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidate')">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  props: ["dataObj"],
  created() {
    if (this.dataObj.data) {
        this.formValidate = JSON.parse(JSON.stringify(this.dataObj.data));
        this.newItem = false;
    }
  },
  data() {
    const validatorLevelCode = (rule, value, callback) =>{
      let url = `/model/orgRisklevel/isHaveCompLevel/${value}`
      if (this.newItem) {
        this.axios
          .get(url)
          .then(response => {
            if (response.data == "false") {
              callback(new Error("等级代码已存在"));
            } else {
              callback();
            }
          })
          .catch(error => {});
      } else {
        callback();
      }
    };
    return {
      formValidate: {
        levelCode: "",
        color: "",
        levelName: "",
        evalCycle: "",
        controlMeasure: "",
      },
      newItem: true,
      typeNm: "",//业务/产品
      ruleValidate: {
        levelCode: [
          {
            required: true,
            message: "等级代码不能为空！",
            trigger: "blur"
          },
          {
            type: "string",
            max: 32,
            message: "等级代码不能超过32个字！",
            trigger: "blur"
          },
          {
            validator: validatorLevelCode,
            trigger: "xxxx"
          }
        ],
        levelName: [
          {
            required: true,
            message: "等级名称不能为空！",
            trigger: "blur"
          },
          {
            type: "string",
            max: 128,
            message: "等级名称不能超过128个字！",
            trigger: "blur"
          }
        ],
        evalCycle: [
          {
            pattern: /^[1-9]+[0-9]*$/,
            message: "请输入正整数",
            trigger: "blur"
          },
          {
            validator: (rule, value, callback) => {
                        if (!value){
                            callback();
                            return;
                        }
                        if (parseInt(value) > 1000){
                            callback("最大不能超过1000");
                        }else {
                          callback();
                        }
            },
            trigger: "blur"
          }
        ],
        controlMeasure: [
          {
            type: "string",
            max: 512,
            message: "控制措施不能超过512个字！",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    handleSubmit(name) {
      let data = JSON.stringify(this.formValidate);
      let url = "/model/orgRisklevel/saveOrgCompLevel";
      this.$refs[name].validate(valid => {
        if (valid) {
            this.axios
            .post(url, data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.$Message.success("保存成功");
                this.$emit("save");
              }
            })
            .catch(error => {});
        }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    }
  }
};
</script>
<style lang="scss" @scoped>
.add-risk-level {
  margin: 0 30px;
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
  }
}
</style>


