<template>
  <div class="risk-level-set1">
    <div class="topTitle">
      <span class="topTitle-span1">模型管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">机构洗钱风险模型管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">风险等级设置</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>综合评估等级设置
          <div class="button-content">
            <Button type="primary" @click="operate('1')" v-authcontrol="{menuCode:'111003001001'}">
              <i class="ivu-icon ivu-icon-md-add"></i>新增
            </Button>
            <Button type="primary" @click="operate('5')" v-authcontrol="{menuCode:'111003001004'}">
              <Icon type="ios-alarm-outline" />历史
            </Button>
            <Button type="primary" @click="exportFn(1)" v-authcontrol="{menuCode:'111003001005'}">
              <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
            </Button>
          </div>
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader1">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                  <tr>
                    <td>{{index+1}}</td>
                    <td>
                      <span class="spanBox" @click="operate('2', item)" v-authcontrol="{menuCode:'111003001002'}">修改</span>
                      <span class="spanBox" @click="operate('3', item)" v-authcontrol="{menuCode:'111003001003'}">删除</span>
                    </td>
                    <td>{{item.levelCode}}</td>
                    <td>{{item.levelName}}</td>
                    <td>{{item.evalCycle}}</td>
                    <td>
                      <span class="spanBox" @click="operate('4', item)">详情</span>
                    </td>
                  </tr>
                </template>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>固有风险等级设置
          <div class="button-content">
            <Button type="primary" @click="operate2('1')" v-authcontrol="{menuCode:'111003001009'}">
              <i class="ivu-icon ivu-icon-md-add"></i>新增
            </Button>
            <Button type="primary" @click="operate2('5')" v-authcontrol="{menuCode:'111003001012'}">
              <Icon type="ios-alarm-outline" />历史
            </Button>
           
            <Button type="primary"  @click="exportFn(2)" v-authcontrol="{menuCode:'111003001013'}">
              <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
            </Button>
          </div>
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody2">
                  <tr>
                    <td>{{index+1}}</td>
                    <td>
                      <span class="spanBox" @click="operate2('2', item)" v-authcontrol="{menuCode:'111003001010'}">修改</span>
                      <span class="spanBox" @click="operate2('3', item)" v-authcontrol="{menuCode:'111003001011'}">删除</span>
                    </td>
                    <td>{{item.levelCode}}</td>
                    <td>{{item.levelName}}</td>
                    <td>{{item.minValue}}</td>
                    <td>{{item.maxValue}}</td>
                    <td>
                      <span class="spanBox" @click="operate2('4', item)">详情</span>
                    </td>
                  </tr>
                </template>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>控制措施有效性评估等级设置
          <div class="button-content">
            <Button type="primary" @click="operate3('1')" v-authcontrol="{menuCode:'111003001016'}">
              <i class="ivu-icon ivu-icon-md-add"></i>新增
            </Button>
            <Button type="primary" @click="operate3('5')">
              <Icon type="ios-alarm-outline" />历史
            </Button>
            <Button type="primary"  @click="exportFn(3)" v-authcontrol="{menuCode:'111003001020'}">
              <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
            </Button>
          </div>
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody3">
                  <tr>
                    <td>{{index+1}}</td>
                    <td>
                      <span class="spanBox" @click="operate3('2', item)" v-authcontrol="{menuCode:'111003001017'}">修改</span>
                      <span class="spanBox" @click="operate3('3', item)" v-authcontrol="{menuCode:'111003001018'}">删除</span>
                    </td>
                    <td>{{item.levelCode}}</td>
                    <td>{{item.levelName}}</td>
                    <td>{{item.minValue}}</td>
                    <td>{{item.maxValue}}</td>
                    <td>
                      <span class="spanBox" @click="operate3('4', item)">详情</span>
                    </td>
                  </tr>
                </template>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
    <Modal :mask-closable="false"  
      v-model="showAddModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '60%'}"
    >
      <component :is="tabview" @cancel="cancelDialog" @save="saveDatas" :dataObj="dataObj" width="80%" v-if="showAddModal"></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import addRiskLevelComp from "../common/riskLevelSet/addRiskLevelComp";
import addRiskLevelSimp from "../common/riskLevelSet/addRiskLevelSimp";
import history2 from "../common/riskLevelSet/history2";
import riskLevelDetail from "../common/riskLevelSet/riskLevelDetail";
export default {
  created() {
      this.axios
      .get("/model/orgRisklevel/getRiskLevel")
      .then(response => {
        if (response.code == "200") {
          this.tableBody = response.data.orgCompLevelVo;
          this.tableBody2 = response.data.orgRiskLevel;
          this.tableBody3 = response.data.orgContrRiskLevel;
        }
      })
      .catch(error => {});
  },
  components: {
    addRiskLevelComp,
    addRiskLevelSimp,
    riskLevelDetail,
    history2
  },
  data() {
    return {
      tabview:'addRiskLevelComp',
      showAddModal: false,
      dataObj:{
        data:{},
      },
      modelTitle: "新增",
      type: this.$route.params.type,
      tableHeader1: [
        { name: "序" },
        { name: "操作" },
        { name: "等级代码" },
        { name: "等级名称" },
        { name: "评定周期（月）" },
        { name: "控制措施" }
      ],
      tableHeader: [
        { name: "序" },
        { name: "操作" },
        { name: "等级代码" },
        { name: "等级名称" },
        { name: "下限分值" },
        { name: "上限分值" },
        { name: "控制措施" }
      ],
      tableBody: [
      ],
      tableBody2: [
      ],
      tableBody3: [
      ]
    };
  },
  methods:{
    cancel() {},
    operate(type, item){
      switch (type) {
        case '1':
          this.modelTitle = '新增'
          this.tabview = 'addRiskLevelComp';
          this.dataObj = {
          };
          this.showAddModal = true;
          break;
        case '2':
          this.modelTitle = '修改'
          this.dataObj = {
            data:item
          };
          this.tabview = 'addRiskLevelComp';
          this.showAddModal = true;
          break;
        case '3':
          this.$Modal.confirm({ 
            title: "是否删除？",
            okText: "确认",
            cancelText: "取消",
            onOk: () => {
                this.axios
                .get(`/model/orgRisklevel/deleteOrgCompLevel/${item.iD}`)
                .then(response => {
                  if (response.code == "200") {
                      this.$Message.success("操作成功");
                      this.axios
                      .get("/model/orgRisklevel/getRiskLevel")
                      .then(response => {
                        if (response.code == "200") {
                          this.tableBody = response.data.orgCompLevelVo;
                          this.tableBody2 = response.data.orgRiskLevel;
                          this.tableBody3 = response.data.orgContrRiskLevel;
                        }
                      })
                      .catch(error => {});
                  }
                })
                .catch(error => {});
            },
            onCancel: () => {
            }
          });
          break;
        case '4':
          this.modelTitle = '控制措施详情'
          this.tabview = 'riskLevelDetail';
          this.dataObj = {
            data:item
          };
          this.showAddModal = true;
          break;
        case '5':
          this.modelTitle = '历史详情';
          this.dataObj = {
            type:'1'
          };
          this.tabview = 'history2';
          this.showAddModal = true;
          break;
      }
    },
    operate2(type, item){
      switch (type) {
        case '1':
          this.modelTitle = '新增'
          this.tabview = 'addRiskLevelSimp';
          this.showAddModal = true;
          this.dataObj = {
            type:'2',
          };
          break;
        case '2':
          this.modelTitle = '修改'
          this.dataObj = {
            type:'2',
            data:item
          };
          this.tabview = 'addRiskLevelSimp';
          this.showAddModal = true;
          break;
        case '3':
          this.$Modal.confirm({ 
            title: "是否删除？",
            okText: "确认",
            cancelText: "取消",
            onOk: () => {
                this.axios
                .get(`/model/orgRisklevel/deleteOrgRiskLevel/${item.iD}`)
                .then(response => {
                  if (response.code == "200") {
                      this.$Message.success("操作成功");
                      this.axios
                      .get("/model/orgRisklevel/getRiskLevel")
                      .then(response => {
                        if (response.code == "200") {
                          this.tableBody = response.data.orgCompLevelVo;
                          this.tableBody2 = response.data.orgRiskLevel;
                          this.tableBody3 = response.data.orgContrRiskLevel;
                        }
                      })
                      .catch(error => {});
                  }
                })
                .catch(error => {});
            },
            onCancel: () => {
            }
          });
          break;
        case '4':
          this.modelTitle = '控制措施详情'
          this.tabview = 'riskLevelDetail';
          this.dataObj = {
            data:item
          };
          this.showAddModal = true;
          break;
        case '5':
          this.modelTitle = '历史详情';
          this.dataObj = {
            type:'2'
          };
          this.tabview = 'history2';
          this.showAddModal = true;
          break;
      }
    },
    operate3(type, item){
      switch (type) {
        case '1':
          this.modelTitle = '新增'
          this.tabview = 'addRiskLevelSimp';
          this.showAddModal = true;
          this.dataObj = {
            type:'3',
          };
          break;
        case '2':
          this.modelTitle = '修改'
          this.dataObj = {
            type:'3',
            data:item
          };
          this.tabview = 'addRiskLevelSimp';
          this.showAddModal = true;
          break;
        case '3':
          this.$Modal.confirm({ 
            title: "是否删除？",
            okText: "确认",
            cancelText: "取消",
            onOk: () => {
                this.axios
                .get(`/model/orgRisklevel/deleteContrRiskLevel/${item.iD}`)
                .then(response => {
                  if (response.code == "200") {
                      this.$Message.success("操作成功");
                      this.axios
                      .get("/model/orgRisklevel/getRiskLevel")
                      .then(response => {
                        if (response.code == "200") {
                          this.tableBody = response.data.orgCompLevelVo;
                          this.tableBody2 = response.data.orgRiskLevel;
                          this.tableBody3 = response.data.orgContrRiskLevel;
                        }
                      })
                      .catch(error => {});
                  }
                })
                .catch(error => {});
            },
            onCancel: () => {
            }
          });
          break;
        case '4':
          this.modelTitle = '控制措施详情'
          this.tabview = 'riskLevelDetail';
          this.dataObj = {
            data:item
          };
          this.showAddModal = true;
          break;
        case '5':
          this.modelTitle = '历史详情';
          this.dataObj = {
            type:'3'
          };
          this.tabview = 'history2';
          this.showAddModal = true;
          break;
      }
    },
    exportFn(type){
      let url = "/model/orgRisklevel/doExportContrRiskLevel";
      if (type == "1")
        url = "/model/orgRisklevel/doExportCompRiskLevel";
      else if (type == "2")
        url = "/model/orgRisklevel/doExportInhRiskLevel";
      this.axios
        .get(url,{responseType: "blob"})
        .then(response => {
          this.download(response,'导出文档.xls');
        })
        .catch(error => {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    saveDatas(){
      this.showAddModal = false;
      this.axios
      .get("/model/orgRisklevel/getRiskLevel")
      .then(response => {
        if (response.code == "200") {
          this.tableBody = response.data.orgCompLevelVo;
          this.tableBody2 = response.data.orgRiskLevel;
          this.tableBody3 = response.data.orgContrRiskLevel;
        }
      })
      .catch(error => {});
    },
    cancelDialog(val) {
      this.showAddModal = val;
    },
  },
};
</script>
<style lang="scss" scoped>
.risk-level-set1 {
  .button-content {
    float: right;
  }
}
</style>


